<template>
<div class="ui two divided grid">
  <div class="row content">
    <div class="book-page five wide column">
      <h1>Book Check Price</h1>
      <h2>{{ form.value }}</h2>
      <form class="ui form" v-on:submit.prevent="search(form.value)">
        <div class="field">
          <input type="text" v-model="form.value" name="first-name" placeholder="Search by">
        </div>
      </form>
    </div>
    <div class="old-pattern eleven wide column">
      <div class="ui cards book-card">
        <div v-for="(book, index) in orderedBooks" :key="index" class="card">
          <div class="content segment">
            <img class="ui small centered image" :src="`${baseUrl}library/${book.store}.png`">
            <div class="description">
              {{ book.name }}
              <img class="right floated tiny ui image" v-bind:src="book.image">
            </div>
            <div class="header">
              <template v-if="isPrice(book.price)">
                {{ book.price | currency }}
              </template>
              <template v-else>
                Fora de Estoque
              </template>
            </div>
            <div class="ui two buttons">
              <a target="_blank" v-bind:href="book.url" class="ui basic green button">Open</a>
            </div>
          </div>
        </div>
      </div>
    </div>  
  </div>
</div>
</template>

<script>
import axios from "axios";
import _ from "lodash";
import { stores } from "./stores";
//import { saraiva } from "./stores";

export default {
  data: function() {
    return {
      state: {
        books: []
      },
      form: {
        value: null
      },
      baseUrl: process.env.BASE_URL
    };
  },
  computed: {
    orderedBooks: function() {
      return _.orderBy(this.state.books, ["price"]);
    }
  },
  methods: {
    isPrice: function(str) {
      return _.isNumber(str);
    },
    search: function(value) {
      this.state.books = [];
      this.aggregate(value);
    },
    aggregate: function(value) {
      stores.forEach(store => {
        const url = `http:localhost:5000/api/${store}/book/${value}`;
        axios
          .get(url)
          .then(r => (this.state.books = [...r.data, ...this.state.books]));
      });
    }
  }
};
</script>

<style>
.content {
  height: 100vh;
}
.book-card {
  max-height: 40vh;
}
.book-page {
  background-color: #f5f5f5;
  text-align: center;
}
.old-pattern {
  background-color: #6a7d8e;
  overflow-y: scroll;
}
</style>


